body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$anime-color: #ff9b51;

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

input {
  padding: 0;
  &:focus {
    outline: none;
  }
}

a {
  text-decoration: none;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.section {
  width: 100%;
  height: 100vh;

  .section__white {
    background-color: #fff;
  }
  .section__gray {
    background-color: #f5f5f5;
  }
  .container {
    max-width: 1100px;
    margin: 0 auto;
  }
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  background-color: #000;
  z-index: 50;
}

.standart {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  top: 70px;
  left: 0;
  right: 0;
  width: 100%;
  height: calc(100vh - 150px);
}

.standart--gray {
  background-color: #f5f5f5;
}

.standart--pricing {
  background-color: #f5f5f5;
  align-items: flex-start;
}

.standart--white {
  background-color: #fff;
}
